<template>
  <div>
    <div class="top">
      <div class="login" @click="toLogin">
        <div class="img">
          <img
            src="https://source.qunarzz.com/usercenter/touch/avatar.png"
            alt=""
          />
        </div>
        <p v-if="username != '登录/注册'">{{username}}</p>
        <p v-else>登录/注册</p>
      </div>
      <div @click="exit" class="exit">退出</div>
    </div>
  </div>
</template>

<script>
export default {
    data() {
      return {
        username:'',
        arr:[]
      }
    },
    created() {
      if(JSON.parse(localStorage.getItem('list'))){
        this.username = JSON.parse(localStorage.getItem('list')).username
      }else{
        this.username = '登录/注册'
      }
    },
    watch: {
      '$route'(to){
        if(to.path == '/mine'){
          this.username = JSON.parse(localStorage.getItem('list')).username;
        }
      }
    },
    methods: {
        toLogin(){
            if(this.$store.state.username == '登录/注册'){
                this.$router.push({path:'login'});
            }
        },
        exit(){
           localStorage.removeItem('list');
           this.$router.go(0);
        }
    }
}
</script>

<style scoped>
.top {
  width: 100%;
  height: 4rem;
  background: url("https://s.qunarzz.com/usercenter_mobile/images/my/mybgnew-20161111.jpg")
    no-repeat;
  background-size: 100% 4rem;
  position: relative;
}
.top>.login{
    width: 100%;
    position: absolute;
  bottom: 0.8rem;
  left: 0;
  right: 0;
  text-align: center;
  font-size: 0.266667rem;
  color: #fff;
  
}
.top >.login>.img {
  width: 1.333333rem;
  height: 1.333333rem;
  margin: 0.133333rem auto;

}
.top >.login>.img img {
  width: 100%;
  height: 100%;
}
.top>.exit{
    position: absolute;
    top: 0.4rem;
    right: 0.4rem;
    font-size: 0.266667rem;
    color: #fff;
}
</style>
